let SlidersArray = []

function sliderCategoriesCard() {
    $(".categories-card__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.categories-card__slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: '1',
            spaceBetween: 15,
            observer: true,
            observeParents: true,
            pagination: {
                el: `.categories-card__slider[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
        })
    })
}


$(document).ready(function() {
    sliderCategoriesCard();

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    $(".tab2").click(function () {
        let path = $(this).attr("data-tab-path2")
        $(this).siblings(".tab2").removeClass("tab2--active")
        $(this).addClass("tab2--active")

        $(this).parents(".tabs-inner2").find(".tab__content2").removeClass("tab__content2--active")
        $(this).parents(".tabs-inner2").find(`.tab__content2[data-content-path2="${path}"]`).addClass("tab__content2--active")
    })

    // Accordion
    $(".item-show").click((function() {
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

    // Checkbox
    $('.select-all').click(function () {
        $(this).toggleClass('select-all--active')
        $('.selections__subitem').toggleClass('selections__subitem--active')
        if ($('.selections__subitem').hasClass('selections__subitem--active')) {
            $('.selections__subitem').addClass('selections__subitem--active')
        }
    })

    $('.selections__subitem').click(function () {
        $(this).toggleClass('selections__subitem--active')
        if ($('.selections__subitem').not('selections__subitem--active')) {
            $('.select-all').removeClass('select-all--active')
        }
    })

    $('.selected-ones').click(function () {
        $('.selections__subitem--active').remove()
        if ($('.selections__subitem').length) {

        } else {
            $('.selections__items').remove()
        }
    })

    // $('.selections__subitem-delete').click(function () {
    //     $(this).parents('.selections__subitem').remove()
    // })

    // All-show
    $('.all-show__btn').click(function () {
        $(this).toggleClass('all-show__btn--active')
        $('.colors-checkboxes').toggleClass('colors-checkboxes--active')
        $('.products__list').toggleClass('products__list--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').toggleClass('select__wrap--active')
    })

    $('.select__list .categories-sidebar__checkbox').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        // $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list .categories-sidebar__checkbox").removeClass("active")
        $(this).addClass("active")
    })

    $('.table__select2-li').click(function name(params) {
        $('.select__wrap').removeClass('select__wrap--active')
    })

    $('.select-btn').click(function () {
        selectWrap.removeClass('select__wrap--active')
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Input from before
    $(".from-before__input").on("keyup", (function(e) {
        $(this).siblings(".input-title").removeClass("input-title--active")
         "" !== $(this).val() && $(this).siblings(".input-title").addClass("input-title--active")
    }))

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
    }

})

